import BaseContent from "./base-content";
import {Loc, Tag} from "main.core";
import {Button} from "ui.buttons";
import {EventEmitter} from "main.core.events";
import {Popup} from "main.popup";
import {ajax} from "main.core";
export class Activate extends BaseContent
{
	getContent(): HTMLElement
	{
		return Tag.render`
			<div>
				<center>
				<h1>Активоровать ключ</h1>
				<h5>Это может сделать только пользователь с правами администратора</h5>
				</center>
				<form id="intranet-license-activate-key-form" name="testForm">
				<div class="ui-form">
					<div class="ui-form-row">
					  <div class="ui-form-label">
					   <div class="ui-ctl-label-text">Логин администратора</div>
					  </div>
					  <div class="ui-form-content">
					   <div class="ui-ctl ui-ctl-textbox ui-ctl-w100">
					    <input type="text" name="login" class="ui-ctl-element" placeholder="Admin">
					   </div>
					  </div>
					 </div>
					 <div class="ui-form-row">
					  <div class="ui-form-label">
					   <div class="ui-ctl-label-text">Пароль</div>
					  </div>
					  <div class="ui-form-content">
					   <div class="ui-ctl ui-ctl-textbox ui-ctl-w100">
					    <input type="password" name="password" class="ui-ctl-element" placeholder="**********">
					   </div>
					  </div>
					 </div>
					
					<div class="ui-form-row">
					  <div class="ui-form-label">
					   <div class="ui-ctl-label-text">Личензионный ключ или купон</div>
					  </div>
					  <div class="ui-form-content">
					   <div class="ui-ctl ui-ctl-textbox ui-ctl-w100">
					    <input type="text" name="key" class="ui-ctl-element" placeholder="XXXX-XXXX-XXXX-XXXX-XX">
					   </div>
					  </div>
					 </div>
					</div>
				</form>
			</div>
`;
	}

	getButtonCollection(): Array
	{
		const backBtn = new Button({
			text: '',
			noCaps: true,
			round: true,
			className: 'license-popup-back-btn',
			icon: BX.UI.Button.Icon.BACK,
			color: BX.UI.Button.Color.LIGHT_BORDER,
			size: BX.UI.Button.Size.MEDIUM,
			tag: BX.UI.Button.Tag.BUTTON,
			onclick: () => {
				EventEmitter.emit(EventEmitter.GLOBAL_TARGET, 'IntranetLicensePopup:back', {
					source: this,
				});
			},
		});
		const activateBtn = new Button({
			text: Loc.getMessage('INTRANET_LICENSE_POPUP_BUTTON_ACTIVATE'),
			noCaps: false,
			round: true,
			size: BX.UI.Button.Size.MEDIUM,
			color: BX.UI.Button.Color.SUCCESS,
			tag: BX.UI.Button.Tag.BUTTON,
			onclick: () => {
				console.log('do request')
				const formNode = document.querySelector('#intranet-license-activate-key-form');
				const formData = new FormData(formNode);
				// console.log(formNode);
				console.log(...formData);
				// console.log(new URLSearchParams(formData).toString());
				// this.request(formData);
			},
		});

		const helpBtn = new Button({
			text: Loc.getMessage('INTRANET_LICENSE_POPUP_BUTTON_NEED_HELP'),
			noCaps: true,
			round: true,
			className: 'license-popup-link-btn',
			link: 'https://dev.1c-bitrix.ru/learning/course/index.php?COURSE_ID=135&LESSON_ID=25720',
			size: BX.UI.Button.Size.EXTRA_SMALL,
			color: BX.UI.Button.Color.LINK,
			tag: BX.UI.Button.Tag.LINK,
			onclick: () => {
				console.log('do request')
			},
		});

		return [backBtn, activateBtn, helpBtn];
	}

	init(popup: Popup): void
	{
		popup.setContent(this.getContent());
		popup.setButtons(this.getButtonCollection());
	}

	request(data): void
	{
		ajax.runComponentAction('bitrix:intranet.license.popup', 'activate', {
			mode: 'class',
			data: data
		}).then((response) => {
			console.log(response);
		}, (response) => {
			console.error(response);
		});
	}
}