import BaseContent from "./base-content";
import {Loc, Tag} from "main.core";
import {Button} from "ui.buttons";
import {EventEmitter} from "main.core.events";
import {Popup} from "main.popup";
import {Activate} from "./activate";
export class ExpiredLicense extends BaseContent
{
	getContent(): HTMLElement
	{
		return Tag.render`
			<div style="margin: 13px; height: 85%; background-color: #dbf2f9;">
				<center>
					<h1>Доступ временно закрыт</h1>
					<div>
						<p>
						Срок действия лицунзии заканчился, для дальнейшей работы вам нежно продлить подписку. 
						Не волнуйтесь, все ваши данные созранились. 
						После покупки активировать ключ продления сможет администратор вашего Битрикс24
						</p>
					</div>
				</center>
			</div>
`;
	}

	getButtonCollection(): Array
	{
		const buyBtn = new Button({
			text: Loc.getMessage('INTRANET_LICENSE_POPUP_BUTTON_RENEW'),
			noCaps: false,
			round: true,
			link: 'https://www.1c-bitrix.ru/personal/order/basket.php',
			size: BX.UI.Button.Size.MEDIUM,
			color: BX.UI.Button.Color.SUCCESS,
			tag: BX.UI.Button.Tag.LINK,
		});
		const activateBtn = new Button({
			text: Loc.getMessage('INTRANET_LICENSE_POPUP_BUTTON_ACTIVATE_KEY'),
			noCaps: false,
			round: true,
			size: BX.UI.Button.Size.MEDIUM,
			color: BX.UI.Button.Color.PRIMARY,
			tag: BX.UI.Button.Tag.BUTTON,
			onclick: () => {
				console.log('event activate');
				EventEmitter.emit(EventEmitter.GLOBAL_TARGET, 'IntranetLicensePopup:changeContent', {
					source: this,
					target: new Activate
				});
			},
		});

		return [buyBtn, activateBtn];
	}

	init(popup: Popup): void
	{
		popup.setContent(this.getContent());
		popup.setButtons(this.getButtonCollection());
	}
}